function throttle(func, wait){
    //
    var oldTime=new Date();

    return function() {
        var _this=this;
        var args=arguments;

        //
        var newTime=new Date();
        //wait
        if(newTime-oldTime>wait) {
            //
            func.apply(_this,args)
            // console.log(",time=",newTime)
            //
            oldTime=newTime;
        }
    }
}

export  {throttle};