import { createApp } from 'vue'
import { createStore } from 'vuex'


//md
import VueMarkdownEditor from '@kangc/v-md-editor';
import VMdPreview from '@kangc/v-md-editor/lib/preview';
import '@kangc/v-md-editor/lib/style/base-editor.css';
import vuepressTheme from '@kangc/v-md-editor/lib/theme/vuepress.js';
import '@kangc/v-md-editor/lib/theme/style/vuepress.css';
import Prism from 'prismjs';
//todo-list
import createTodoListPlugin from '@kangc/v-md-editor/lib/plugins/todo-list/index';
import '@kangc/v-md-editor/lib/plugins/todo-list/todo-list.css';
//
// import createMermaidPlugin from '@kangc/v-md-editor/lib/plugins/mermaid/cdn';
// import '@kangc/v-md-editor/lib/plugins/mermaid/mermaid.css';
//
import createCopyCodePlugin from '@kangc/v-md-editor/lib/plugins/copy-code/index';
import '@kangc/v-md-editor/lib/plugins/copy-code/copy-code.css';


import App from './App.vue'
import router from './router'

VueMarkdownEditor.use(vuepressTheme, {
  Prism,
});

//
VueMarkdownEditor.use(createTodoListPlugin());
// VueMarkdownEditor.use(createMermaidPlugin());
VMdPreview.use(vuepressTheme, {
  Prism,
});
VMdPreview.use(createTodoListPlugin());
VMdPreview.use(createCopyCodePlugin());
// VMdPreview.use(createMermaidPlugin());

//  store 
const store = createStore({
    state () {
      return {
        token:undefined,
        uid:undefined,
        username:undefined,
        premisson:'user',
        maskShow:false,
        loginShow:false,
        recorderShow:false,
        timerShow:false,
        setTime:0,
        hintText:"",
        preUrl:process.env.VUE_APP_URL,
        selectedTags:[],
        mode:'pc',
        log:''
      }
    },
    mutations: {
    // log
      setLog(state, msg){
        state.log+=new Date()+msg+'\n';
      },
    // mode
      setMode(state, mode){
        state.mode= mode;
      },

    // token
      setToken (state, token){
        state.token=token
      },
    //  uid 
      setUid (state,uid) {
        state.uid=uid
      },
    //  username
      setUsername (state,username) {
        state.username=username
      },
    //   
      clear(state){
        state.token=undefined
        state.uid=undefined
        state.username=undefined
      },
      // 
      openMask(state){
        state.maskShow=true
      },
      // 
      closeMask(state){
        state.maskShow=false
      },
      // 
      openLogin(state){
        state.loginShow=true
      },
      // 
      closeLogin(state){
        state.loginShow=false
      },
      // 
      openRecorder(state){
        state.recorderShow=true
      },
      // 
      closeRecorder(state){
        state.recorderShow=false
      },
      // 
      openTimer(state){
        state.timerShow=true
      },
      // 
      closeTimer(state){
        state.timerShow=false
      },
      // 
      setSetTime(state,value){
        state.setTime=value
      },
      // 
      clearSetTime(state){
        state.setTime=0
      },
      //
      setHintText(state,text){
        state.hintText=text
      },
      //
      clearHint(state){
        state.hintText=""
      },
      //
      updateTags(state,newTags){
        state.selectedTags=newTags;
      },
      //
      cleartags(state){
        state.selectedTags=[];
      }
    }
})

export default store
createApp(App).use(VueMarkdownEditor).use(VMdPreview).use(router).use(store).mount('#app')